@import "variables";
@import "node_modules/bootstrap/scss/bootstrap";

//
// Reboot
//
body {
	color: $text;
}

h1 {
	font-weight: $font-weight-bold;
	line-height: 2.6;
}

h2 {
	font-weight: $font-weight-bold;
	line-height: 2;
}

h3 {
	font-weight: $font-weight-bold;
	line-height: 1.8;
}

h4 {
	line-height: 1.3;
}

p {
	font-size: 0.625rem;
	line-height: 1.8;
}

label {
	font-size: 0.75rem;
}

.sr-only {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0, 0, 0, 0);
	border: 0;
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
	position: static;
	width: auto;
	height: auto;
	margin: 0;
	overflow: visible;
	clip: auto;
}

//
// Buttons
//
.btn {
	padding: 0.532rem 1.5rem;
}

.btn-svg {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 0;
}

.btn {
	font-size: 0.75rem;
	font-weight: $font-weight-bold;
	line-height: 1.8;
	text-transform: uppercase;
}

.btn-secondary-light,
.btn-secondary-light:hover {
	color: $white;
}

.btn-dropdown {
	background-color: #FBECED;
	color: $secondary;

	&:hover {
		color: $secondary;
	}

	&:focus {
		box-shadow: 0 0 0 0.25rem scale-color($secondary, $lightness: 75%);
	}
}

@each $color, $value in $theme-colors {
	.btn-#{$color} {
		&-text {
			padding: 0;
			color: $value;
			background: transparent;
			font-size: 0.75rem;
			font-weight: $font-weight-bold;
			text-transform: uppercase;

			&:hover {
				color: darken($value, 10%);
			}

			&:before {
				display: inline-block;
				margin-right: 0.25rem;
				content: "+";
			}
		}

		&:disabled,
		&.disabled {
			background-color: transparent;
			border: 2px solid $value;
			color: $value;
		}
	}
}

//
// Cards
//
.card {
	border-radius: 1.5rem;
	border-color: transparent;
	box-shadow: 0 3px 28px 0 rgba(40, 115, 128, 0.2);

	&-body {
		padding: 1.25rem;
	}

	&-title {
		margin-bottom: 1.25rem;
	}

	&-subtitle {
		margin-bottom: 0.3rem;
	}

	&-text {
		margin-bottom: 0.625rem;
	}

	hr {
		margin: 0.625rem 0;
	}

	.accordion {
		border-radius: 0;

		&-item {
			border: none !important;
			border-radius: 0;
		}

		&-button {
			font-weight: $font-weight-bold;
		}

		&-button:not(.collapsed, :focus-visible) {
			box-shadow: none;
		}
	}
}

.card.card-primary,
.card.card-primary-light {
	&.card.card-primary {
		background-color: $primary;
	}
	&.card.card-primary-light {
		background-color: $primary-light;
	}
	.card-image {
		display: inline-flex;
		align-items: center;
		justify-content: flex-end;
	}

	.card-body {
		position: relative;

		padding-left: 0;
	}

	&.card-primary .card-body {
		color: $white;
	}

	&.card-primary-light .card-body {
		color: $text;
	}

	.sup-title {
		display: inline-block;
		margin-bottom: 0.4rem;
		padding: 0 0.5rem;
		height: 18px;
		line-height: 18px;
		border-radius: 3px;
		background-color: $secondary-light;
		font-size: 0.75rem;
	}

	.info {
		position: absolute;
		right: 14px;
		top: 14px;
		color: $secondary-light;
		font-size: 1.6rem;
	}
}

//
// Datepickers
//
div.flatpickr-calendar {
	width: 100%;
	box-shadow: none;

	div.flatpickr-months {
		margin-bottom: 1rem;

		.flatpickr-current-month {
			color: $text;
		}

		& .flatpickr-prev-month,
		& .flatpickr-next-month {
			width: 14.2857143%;

			svg,
			svg {
				fill: $secondary;
				stroke-width: 1px;
				stroke: $secondary;
			}

			&:hover svg,
			&:hover svg {
				fill: $secondary-light;
				stroke: $secondary-light;
			}
		}
	}

	.flatpickr-weekday {
		font-size: 0;
	}

	.flatpickr-weekday:first-letter {
		font-size: 0.75rem;
		text-transform: uppercase;
	}

	.flatpickr-current-month span.cur-month {
		&:first-letter {
			text-transform: capitalize;
		}
	}

	.flatpickr-current-month input.cur-year {
		font-weight: $font-weight-bold;
	}

	.flatpickr-rContainer {
		margin: 0 auto;
		width: 100%;
	}

	.flatpickr-days {
		width: 100%;

		.dayContainer {
			width: 100%;
			max-width: 100%;

			.flatpickr-day {
				position: relative;
				width: auto;
				max-width: 100%;
				background: transparent;
				border: none;
				color: $text;
			}

			.flatpickr-day:hover {
				background-color: transparent;
				border: none;
				font-weight: $font-weight-bold;
			}

			.flatpickr-day.prevMonthDay,
			.flatpickr-day.nextMonthDay {
				color: $primary;
			}

			.flatpickr-day.today {
				font-weight: $font-weight-bold;
			}

			.flatpickr-day.today:after {
				display: block;
				content: "•";
				position: absolute;
				left: 50%;
				bottom: 0;
				font-size: 16px;
				transform: translate(-50%, 10px);
			}

			.flatpickr-day.highlighted:before {
				display: block;
				content: " ";
				position: absolute;
				left: 50%;
				top: 50%;
				width: 2rem;
				height: 2rem;
				background-color: $secondary-light;
				border-radius: 50%;
				transform: translate(-50%, -50%);
				z-index: -1;
			}
		}
	}
}

//
// Dropdowns
//
.dropdown {
	@include media-breakpoint-down(sm) {
		.dropdown-menu {
			position: fixed !important;
			margin-left: calc(var(--bs-gutter-x) * .5) !important;
			padding: 1rem;
			width: calc(100vw - var(--bs-gutter-x));
			max-height: 50vh;
			inset: 50% auto auto 0 !important;
			transform: translateY(-50%) !important;
			z-index: 999;
			box-shadow: 0 3px 28px 0 rgba(40, 115, 128, 0.2);
			border: none;
			border-radius: 1.5rem;
		}
	}
	@include media-breakpoint-up(sm) {
		.dropdown-menu {
			padding: 0.532rem 1.5rem;
			width: 100%;
			border-radius: 0;
			border: 0;
			transform: translate(0px, 52px) !important;
		}
	}

	.dropdown-toggle.show:after {
		transform: rotate(180deg);
	}

	.dropdown-toggle:after {
		display: inline-block;
		content: " ";
		width: 1.25rem;
		height: 1.25rem;
		background-image: escape-svg(url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$secondary}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>"));
		border: none;
		border-radius: initial;
		vertical-align: middle;
	}

	.dropdown-menu {
		background-color: scale-color($primary, $lightness: 95%);
		border-radius: 1rem;

		> li {
			padding: 0.5rem 0;
			border-bottom: solid 1px #cccccc;

			&:last-of-type {
				border-bottom: none;
			}
		}

		.dropdown-item {
			font-size: 0.75rem;

			&.active, &:hover {
				background-color: transparent;
				color: currentColor;
				font-weight: $font-weight-bold;
			}
		}
	}
}

//
// Dropdown-selects
//
.dropdown.select {
	width: 100%;

	.dropdown-toggle {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0.532rem 1rem;
		width: 100%;
		height: 2.875rem;
		background-color: scale-color($primary, $lightness: 95%);
		border: none;
		border-radius: 0;
		color: $text;
		font-size: 0.8125rem;
		font-weight: $font-weight-base;
		text-transform: none;
	}

	.dropdown-menu {
		border-radius: 0;
	}
}

//
// Inputs
//
.form-control.is-invalid {
	background-image: none;
	background-color: #FDEEF2;
}

.invalid-feedback {
	color: $secondary;
}

//
// Check/Radio
//
.form-check-box-text {
	position: relative;
	margin-right: 2rem;

	.form-check-input {
		position: absolute;
		width: calc(100% + 2.125em);
		height: 100%;
		border-color: scale-color($primary, $lightness: 75%);

		+ label {
			position: relative;
			height: 2rem;
			color: scale-color($primary, $lightness: 75%);
			font-weight: $font-weight-bold;
			line-height: 2rem;
			font-size: 0.8125rem;
		}

		&:checked {
			background-color: $primary-light;
			background-image: none;
			border-color: $primary-light;

			& + label {
				color: #ffffff;
			}
		}
	}
}

body.safari .form-check-box-text {
	padding-left: 0;
	.form-check-input {
		visibility: hidden;
	}
	label {
		padding: 0 2.125em;
		height: 100%;
		border: 2px solid scale-color($primary, $lightness: 75%);
		border-radius: 0.25em;
	}
	.form-check-input:checked + label {
		background-color: $primary-light;
		background-image: none;
		border-color: $primary-light;
	}
}

.form-check-radio-text {
	position: relative;
	display: inline-flex;
	align-items: center;
	justify-content: center;
	margin: 0;
	padding: 0;
	width: 44px;
	height: 44px;
	text-align: center;

	.form-check-input {
		position: absolute;
		margin-left: 0;
		width: 100%;
		height: 100%;
		background-color: transparent;
		background-image: none;
		border-color: scale-color($primary, $lightness: 75%);
		border-radius: 0.25em;

		&:checked {
			background-color: $primary-light;
			border-color: $primary-light;
			background-image: none;

			& + label {
				color: $white !important;
			}
		}

		& + label {
			position: relative;
			color: scale-color($primary, $lightness: 75%);
			font-size: 1.25rem;
			font-weight: $font-weight-bold;
		}
	}
}

body.safari .form-check-radio-text {
	.form-check-input {
		visibility: hidden;
	}
	label {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		height: 100%;
		background-color: transparent;
		background-image: none;
		border: 2px solid scale-color($primary, $lightness: 75%);
		border-radius: 0.25em;
	}
	.form-check-input:checked + label {
		background-color: $primary-light;
		border-color: $primary-light;
		background-image: none;
	}
}

.form-check-input:disabled {
	border: 2px solid scale-color($secondary-light, $lightness: 50%);
}

//
// Tooltips
//
.tooltip-inner {
	box-shadow: 0 3px 28px 0 rgba(40, 115, 128, 0.2)
}

//
// Tables
//
.table-wrapper {
	padding: 1.875rem;
}

.table {
	font-size: 0.8125rem;
}

.table tr th {
	font-size: 0.75rem;
	text-transform: uppercase;
}

.table .bi {
	font-size: 1.4rem;
}

.table > :not(:last-child) > :last-child > * {
	border-top: 1px dashed #e6e6e6;
	border-bottom: 1px dashed #e6e6e6;
	color: $primary;
}

.table > :not(caption) > * > * {
	vertical-align: middle;
}