//
// Color system
//

// scss-docs-start gray-color-variables
$white:    #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black:    #000 !default;
// scss-docs-end gray-color-variables

// fusv-disable
// scss-docs-start gray-colors-map
$grays: (
		"100": $gray-100,
		"200": $gray-200,
		"300": $gray-300,
		"400": $gray-400,
		"500": $gray-500,
		"600": $gray-600,
		"700": $gray-700,
		"800": $gray-800,
		"900": $gray-900
) !default;
// scss-docs-end gray-colors-map
// fusv-enable

// scss-docs-start color-variables
$blue:    #0d6efd !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #d63384 !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #ffc107 !default;
$green:   #198754 !default;
$teal:    #20c997 !default;
$cyan:    #0dcaf0 !default;
// scss-docs-end color-variables

// scss-docs-start colors-map
$colors: (
		"blue":       $blue,
		"indigo":     $indigo,
		"purple":     $purple,
		"pink":       $pink,
		"red":        $red,
		"orange":     $orange,
		"yellow":     $yellow,
		"green":      $green,
		"teal":       $teal,
		"cyan":       $cyan,
		"white":      $white,
		"gray":       $gray-600,
		"gray-dark":  $gray-800
) !default;
// scss-docs-end colors-map

// scss-docs-start theme-color-variables
$primary:         #287380 !default;
$primary-light:   #7ED7C2 !default;
$secondary:       #B03661 !default;
$secondary-light: #EEB0B7 !default;
$text:            #485B5C !default;
$success:         $green !default;
$info:            $cyan !default;
$warning:         $yellow !default;
$danger:          $red !default;
$light:           $gray-100 !default;
$dark:            $gray-900 !default;
// scss-docs-end theme-color-variables

// scss-docs-start theme-colors-map
$theme-colors: (
		"primary":          $primary,
		"primary-light":    $primary-light,
		"secondary":        $secondary,
		"secondary-light":  $secondary-light,
		"text":             $text,
		"success":          $success,
		"info":             $info,
		"warning":          $warning,
		"danger":           $danger,
		"light":            $light,
		"dark":             $dark
) !default;
// scss-docs-end theme-colors-map

//
// Typos
//
$font-family-sans-serif: 'Quicksand', sans-serif;

$h1-font-size: 1.25rem;
$h2-font-size: 1rem;
$h3-font-size: 0.875rem;
$h4-font-size: 0.8125rem;

$font-sizes: (
		1: $h1-font-size,
		2: $h2-font-size,
		3: $h3-font-size,
		4: $h4-font-size
);

//
// Grids
//
$grid-columns:        12 !default;
$grid-gutter-width:   1.5rem !default;

//
// Buttons
//
$btn-border-radius: 50rem;

//
// Inputs
//
$input-padding-x:               1rem;
$input-padding-y:               0.844rem;
$input-bg:                      scale-color($primary, $lightness: 90%);
$input-border-width:            0;
$input-border-radius:           0;
$input-color:                   $text;
$input-disabled-bg:             #e9ecef !default;
$input-disabled-border-color:   0;
$input-font-size:               0.8125rem;

//
// Accordions
//
$accordion-button-padding-x:      0;
$accordion-color:                 $text;
$accordion-button-color:          $text;
$accordion-button-active-color:   $text;
$accordion-button-active-bg:      transparent;
$accordion-button-icon:           url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$secondary}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>") !default;
$accordion-button-active-icon:    url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='#{$secondary}'><path fill-rule='evenodd' d='M1.646 4.646a.5.5 0 0 1 .708 0L8 10.293l5.646-5.647a.5.5 0 0 1 .708.708l-6 6a.5.5 0 0 1-.708 0l-6-6a.5.5 0 0 1 0-.708z'/></svg>") !default;

//
// Radios & Checkboxes
//
$form-check-input-bg:                     $white;
$form-check-input-width:                  1.625em;
$form-check-input-bg-color:       $secondary;
$form-check-input-checked-bg-color:       $secondary;
$form-check-input-border:                 2px solid scale-color($secondary, $lightness: 75%);
$form-check-input-checked-color:          $white;
$form-check-input-disabled-opacity:       1;
$form-check-input-checked-bg-image:       url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><path fill='none' stroke='#{$form-check-input-checked-color}' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M6 10l3 3l6-6'/></svg>");
$form-check-radio-checked-bg-image:       url("data:image/svg+xml,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 20 20'><path fill='none' stroke='#{$form-check-input-checked-color}' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M6 10l3 3l6-6'/></svg>");

//
// Tooltips
//
$tooltip-font-size:                 0.75rem;
$tooltip-max-width:                 30rem;
$tooltip-color:                     $text;
$tooltip-bg:                        $white;
$tooltip-border-radius:             1rem;
$tooltip-opacity:                   1;
$tooltip-padding-y:                 1rem;
$tooltip-padding-x:                 1rem;
$tooltip-arrow-width:               1.6rem;
$tooltip-arrow-height:              .8rem;

//
// Tables
//
$table-color:                 $text;
$table-th-font-weight:        700;
$table-border-width:          0;
$table-cell-padding-y:        0.891rem;
$table-cell-padding-x:        1rem;
$table-striped-bg:            scale-color($primary, $lightness: 95%);
$table-striped-order:         even;